      :style="{
        backgroundImage:appData.isDarkMode?getImageURL('file-1707105665561-147318345.png'):getImageURL('file-1707105662869-36372507.png')
      }"
  >
     <p style="background-color: transparent;color: var(--color-neutral-10);">服务器时间 <span>{{ data.all }}</span></p>
      <p style="font-size: 36px;font-weight:500;color: var(--color-neutral-10);">志愿助手数据库管理系统</p>
      <div style="width: 100%;height: 150px;position: absolute;left: 0;bottom: 0;background-position: center;background-size: contain;background-repeat: repeat-x;"
