            <div class="nav-button-inside" style="opacity: 1;background-color: transparent;">
              <span style="color: var(--color-neutral-10);" v-html="item.icon"></span>
              <p style="margin-top: 10px;font-size: 20px;color: var(--color-neutral-10);">{{ item.title }}</p>
              <p style="margin-top: 5px;font-size: 12px;color: var(--color-neutral-7);">{{ item.subTitle }}</p>
              <svg style="position: absolute;right: 15px;bottom: 15px;color: var(--color-neutral-10);" width="20" height="20" viewBox="0 0 48 48" fill="currentColor"><path fill-rule="evenodd" clip-rule="evenodd" d="M24 6v4H6v28h36V28h4v12.2c0 .994-.985 1.8-2.2 1.8H4.2C2.985 42 2 41.194 2 40.2V7.8C2 6.806 2.985 6 4.2 6H24zm20 0a2 2 0 012 2v14h-4v-9.172l-11.95 11.95a1 1 0 01-1.414 0l-1.414-1.414a1 1 0 010-1.414L39.17 9.999 30 10V6h14z" fill="currentColor"/></svg>
